body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-flex-wrap: nowrap;
          flex-wrap: nowrap;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  -webkit-align-content: center;
          align-content: center; }

.form-wrapper {
  max-width: 500px;
  width: 100%;
  margin-left: auto;
  margin-right: auto;
  padding: 50px;
  border-radius: 10px;
  box-shadow: 0px 0px 10px 5px #e7e7e7; }
  .form-wrapper .access_form {
    display: block;
    width: 100%;
    margin-bottom: 40px; }
    .form-wrapper .access_form label {
      display: block;
      font-size: 18px;
      color: #7e7e7e;
      font-weight: bold; }
      .form-wrapper .access_form label input {
        display: block;
        width: 100%;
        box-sizing: border-box;
        padding: 30px 15px;
        font-size: 20px;
        margin-top: 10px;
        margin-bottom: 25px;
        border-radius: 5px;
        border: 1px solid #ccc;
        outline: none;
        text-align: center; }
    .form-wrapper .access_form button {
      display: block;
      padding: 20px 40px;
      max-width: 200px;
      margin-left: auto;
      margin-right: auto;
      background-color: #6b81c7;
      text-transform: uppercase;
      color: #fff;
      font-size: 16px;
      border: none;
      border-radius: 5px;
      cursor: pointer; }
      .form-wrapper .access_form button:hover {
        background-color: #8c9aca; }
  .form-wrapper .response {
    width: 100%;
    border-radius: 10px;
    padding: 50px;
    word-wrap: break-word;
    box-sizing: border-box; }
    .form-wrapper .response.valide {
      background-color: #25a159;
      color: #fff; }
    .form-wrapper .response strong {
      display: block;
      font-weight: 700;
      margin-top: 30px; }

